* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background: #0a0a0a;
  color: #fff;
  overflow-x: hidden;
  height: 100vh;
}

/* Neon text effect */
.neon {
  text-shadow: 
    0 0 10px #FF1744,
    0 0 20px #FF1744,
    0 0 30px #FF1744,
    0 0 40px #FF1744;
  animation: flicker 2s infinite;
}

@keyframes flicker {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.8; }
}

/* Landing page */
.landing {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 2rem;
  text-align: center;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a0a1a 100%);
}

.landing h1 {
  font-size: 3rem;
  margin-bottom: 1.5rem;
  line-height: 1.2;
}

.landing p {
  font-size: 1.2rem;
  opacity: 0.9;
  margin-bottom: 3rem;
  max-width: 500px;
}

.btn-primary {
  background: #FF1744;
  color: white;
  border: none;
  padding: 1rem 3rem;
  font-size: 1.2rem;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s;
  box-shadow: 0 4px 20px rgba(255, 23, 68, 0.5);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 30px rgba(255, 23, 68, 0.7);
}

/* Compass screen */
.compass-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: #0a0a0a;
}

.compass-header {
  padding: 1.5rem;
  text-align: center;
  background: rgba(255, 255, 255, 0.05);
}

.compass-header h2 {
  font-size: 1.5rem;
  color: #FF1744;
}

.compass-main {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.compass {
  width: 280px;
  height: 280px;
  position: relative;
  border: 3px solid #FF1744;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(255, 23, 68, 0.1) 0%, transparent 70%);
  box-shadow: 0 0 40px rgba(255, 23, 68, 0.5);
}

.compass-arrow {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transition: transform 0.3s ease-out;
}

.arrow-shape {
  position: absolute;
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-bottom: 80px solid #FF1744;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
  filter: drop-shadow(0 0 10px rgba(255, 23, 68, 0.8));
}

.distance-display {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  font-weight: bold;
  text-shadow: 0 0 20px rgba(255, 23, 68, 0.8);
}

.compass-footer {
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.05);
  text-align: center;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  overflow: hidden;
  margin-top: 1rem;
}

.progress-fill {
  height: 100%;
  background: #FF1744;
  transition: width 0.3s;
  box-shadow: 0 0 10px rgba(255, 23, 68, 0.8);
}

/* Badge modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.badge-modal {
  background: #1a1a1a;
  padding: 2rem;
  border-radius: 20px;
  text-align: center;
  max-width: 90%;
  animation: slideUp 0.5s;
  border: 2px solid #FF1744;
  box-shadow: 0 0 40px rgba(255, 23, 68, 0.5);
}

@keyframes slideUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.badge-icon {
  width: 200px;
  height: 200px;
  margin: 1rem 0;
  font-size: 8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: bounce 1s;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-30px); }
  60% { transform: translateY(-15px); }
}

.confetti {
  position: absolute;
  width: 10px;
  height: 10px;
  background: #FF1744;
  animation: confettiFall 3s linear;
  pointer-events: none;
}

@keyframes confettiFall {
  to {
    transform: translateY(100vh) rotate(360deg);
    opacity: 0;
  }
}

/* Final form */
.final-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 2rem;
  text-align: center;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a0a1a 100%);
}

.larry-illustration {
  font-size: 10rem;
  margin-bottom: 2rem;
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

.email-form {
  width: 100%;
  max-width: 400px;
}

.email-input {
  width: 100%;
  padding: 1rem;
  font-size: 1.1rem;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid #FF1744;
  border-radius: 10px;
  color: white;
  margin-bottom: 1rem;
}

.email-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

/* Voucher screen */
.voucher-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 2rem;
  text-align: center;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a0a1a 100%);
}

.voucher-code {
  font-family: 'Courier New', monospace;
  font-size: 2rem;
  background: rgba(255, 23, 68, 0.1);
  padding: 1.5rem 2rem;
  border-radius: 10px;
  border: 2px solid #FF1744;
  margin: 2rem 0;
  letter-spacing: 2px;
}

.countdown {
  font-size: 1.2rem;
  opacity: 0.8;
  margin-bottom: 2rem;
}

.share-buttons {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
}

.share-btn {
  padding: 0.8rem 1.5rem;
  border: 2px solid #FF1744;
  background: transparent;
  color: #FF1744;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s;
}

.share-btn:hover {
  background: #FF1744;
  color: white;
}